* {
  box-sizing: border-box;
}

body {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #212121;
  background-color: #fafafa;
  margin: 16px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, Open Sans, Helvetica Neue, sans-serif;
  line-height: 1.5;
}

input {
  font: inherit;
  padding: 8px;
}

button {
  cursor: pointer;
  padding: 8px 12px;
}

.grid {
  flex-wrap: wrap;
  justify-content: center;
  margin: 0 -8px -16px;
  padding: 0;
  list-style: none;
  display: flex;
}

.grid__item {
  min-width: 240px;
  flex-basis: calc(33.3333% - 16px);
  margin-bottom: 16px;
  margin-left: 8px;
  margin-right: 8px;
}

.img {
  max-width: 100%;
  height: 100%;
  display: block;
}

